export default {
	data() {
		return {
			giftList: [],
			page: 1,
			totalPage: 0,
			totalGift: 0,
			text: "",
		};
	},
	onReachBottom() {
		if(this.page < this.totalPage){
			this.page = this.page + 1
			this.GetGiftList()
		}
	},
	onLoad() {
		uni.showLoading({
			title: '请稍等'
		})
		this.GetGiftList()
		this.GetTotalPrice()
	},
	methods:{
		goStaff(index){
			uni.navigateTo({
				url: '../people-detail/people-detail?id=' + this.giftList[index].staffInfo.id
			})
		},
		GetGiftList(){
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				data:{
					page: that.page,
					size: 10
				},
				url: that.Server + 'gift/userList',
				success(res) {
					
					if(res.data.code == 0){
						that.totalPage = res.data.data.totalPage
						for(var i in res.data.data.list){
							that.giftList.push(res.data.data.list[i])
						}
					} else if (res.data.code == 101) {
						uni.showToast({
							title: '登录超时，将重新授权登录',
							duration: 500
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 500)
					} else {
						uni.showModal({
							content: res.data.msg
						})
					}
					uni.hideLoading()
				}
			})
		},
		GetTotalPrice(){
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'gift/total',
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						that.totalGift = res.data.data
						if(res.data.data == 0){
							this.totalGift = 0
						}
					} else if (res.data.code == 101) {
						uni.showToast({
							title: '登录超时，将重新授权登录',
							duration: 500
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 500)
					} else {
						uni.showModal({
							content: res.data.msg
						})
					}
					uni.hideLoading()
				}
			})
		}
	}
}
